@use './colors' as a;
@use './fonts' as f;
@use './media' as m;

%Desc {
	.container {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		@include m.breakpoint(mobile, min) {
			margin-bottom: 20em;
		}

		.header-container {
			display: flex;
			justify-content: center;
			align-items: center;
			background: a.$clr-secondary;
			z-index: 998;
			padding: 5em;

			@include m.breakpoint(mobile, min) {
				width: 621px;
				max-height: 1em;
			}
			@include m.breakpoint(desktop-lg, min) {
				width: 721px;
				max-height: 1em;
			}

			h1 {
			}
		}

		.paragraph-container {
			padding: 3em 1em;
			z-index: 998;
			background: a.$clr-primary-color;

			@include m.breakpoint(mobile, min) {
				margin-top: 6em;
				padding-right: 5em;
				padding-left: 5em;
			}
			@include m.breakpoint(desktop-lg, min) {
				padding: 0 10em;
			}

			h2 {
				color: a.$white;
				margin-bottom: 0.5em;
			}

			p {
				max-width: 55ch;
			}

			blockquote {
				margin-top: 1em;
				margin-left: 1em;
				margin-right: 1em;
				background: a.clr(second, light);
			}
		}
	}
}
%SidePicture {
	position: relative;
	margin-bottom: 1em;
	margin-left: 3.5em;
}
%Picture {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 103px;
	height: 90px;
	border-radius: 3em;
	margin-top: -3em;
	margin-bottom: 3em;
	border: 3px solid a.clr(third);

	box-shadow: 1px 60px 1px 87px a.$clr-secondary;
}
%Card {
	display: flex;
	box-shadow: 0px 4px 5px 1px rgba(0, 0, 0, 0.25);
	flex-direction: column;
	align-items: center;
	line-height: 1.5em;
	position: relative;
	z-index: 1;
	padding: 1.5em 1.5em;
	border-radius: 0.5em;
	background: a.$clr-primary-color;
	@include m.breakpoint(mobile, min) {
	}
}
%Button {
	//Todo: Dela with the div here and add margin top
	position: relative;
	padding-bottom: 0.1em;
	padding-left: 0.5em;
	border: none;
	background: none;
	font-size: 1rem;
	color: a.$white;
	cursor: pointer;
	list-style: none;
	text-decoration: none;

	&::after {
		position: absolute;
		border-radius: 0.1em;
		content: "";
		top: 11.5px;
		right: 5px;
		left: 1px;
		bottom: -3px;
		background: a.$clr-third;
		z-index: -1;
		transform-origin: bottom left;
		transition: transform 175ms cubic-bezier(0.91, 0.01, 0.67, 1.72);
	}
}

%Wheel {
	display: none;
	width: 50px;
	height: 50px;
	object-fit: cover;
	border-radius: 0.5em;
	//box-shadow: inset 5px 5px 10px 2px rgba(0, 0, 0, 0.25);
}
