@use '../abstracts' as a;
body {
	background: a.$clr-primary-color;
	font-family: a.$MainFont;
}

.papa-wrapper {
	position: relative;
	@include a.breakpoint(side, min) {
		&::before,
		&::after {
			position: absolute;
			content: "";
			background: a.$clr-secondary;
			top: 0;
			right: 0;
			z-index: 99;
			height: 100vh;
			width: 34vw;
		}
	}
	@include a.breakpoint(checker, min) {
		.hero-checker {
			display: block !important;
		}
	}

	.hero-checker {
		display: none;
		position: absolute;
		z-index: 999;
		width: 20%;
		height: 20%;
		right: 24%;
		top: 190px;
	}
}
#root {
}
h1,
p {
	color: a.$white;
}

.view-wrapper {
	height: 100vh;
}

text {
	color: a.$white;
}

.MuiOutlinedInput-root.Mui-error .MuiOutlinedInput-notchedOutline {
	border-color: #000000 !important;
}

.MuiFormLabel-root.Mui-error {
	color: #fff !important;
	font-style: a.$MainFont !important;
}

.contact-section .contact-wrapper .send-a-message .contact-form textarea {
	border: none !important;
}
