@use '../abstracts' as a;
.papa-second-wrapper {
	.headroom {
		.ham-menu {
			display: none;
		}
	}
}
.MERN-section {
	@extend %Desc;
	.container {
		@include a.breakpoint(mobile, min) {
			flex-direction: row-reverse;
		}
	}
}

.typescript-section {
	@extend %Desc;
	.container {
		@include a.breakpoint(mobile, min) {
			flex-direction: row;
		}
	}
}

