footer {
	.footer-wrapper {
		margin-bottom: 1em;
	}
	.desc-social-media {
		ul {
			display: flex;
			justify-content: center;

			li {
				list-style: none;
			}

			li a {
				margin-left: 1em;
			}

			img {
				width: 2em;
			}
		}
	}
}
