@use '../abstracts' as a;
footer {
	background: a.$clr-primary-color;
	box-shadow: inset 0px 10px 250px rgba(0, 0, 0, 0.25);
	padding: 1em 1.5em;
	.footer-wrapper {
		color: white;
		text-align: center;
		span {
			color: a.$clr-third;
		}
	}
	img {
		width: 2em;
		height: 2em;
		margin: 0 0.5em;
	}
}
